.course-catalogue-top {
  background: #fff;
  border-radius: 5px;
  padding: 30px 20px;
  display: flex;
}
.course-catalogue-top > div:nth-of-type(1) {
  max-width: 140px;
}
.course-catalogue-top > div:nth-of-type(1) img {
  width: 134px;
  height: 86px;
}
.course-catalogue-top > div:nth-of-type(2) {
  flex: 1;
}
.course-catalogue-top > div:nth-of-type(2) h2 {
  font-size: 20px;
  margin-bottom: 12px;
  color: #2A283D;
  line-height: 100%;
  font-weight: 600;
}
.course-catalogue-top > div:nth-of-type(2) p {
  color: #888;
  margin-bottom: 10px;
  font-size: 16px;
}
.course-catalogue-top > div:nth-of-type(2) p span {
  margin-right: 20px;
}
.course-catalogue-top > div:nth-of-type(2) p:nth-of-type(2) {
  margin: 0;
}
.course-catalogue-top > div:nth-of-type(3) {
  display: flex;
  align-items: flex-end;
  text-align: right;
  justify-content: flex-end;
}
.course-catalogue-top > div:nth-of-type(3) .ant-btn {
  padding: 0px 18px;
  margin: 0 10px;
}
.course-catalogue-main {
  background: #Fff;
  margin: 10px 0;
  padding: 10px 0;
  border-radius: 15px;
  box-shadow: 0 0 3px #eee;
}
.course-catalogue-main .course-template-nav {
  padding: 10px;
  line-height: 50px;
  box-shadow: 0px 3px 3px #eee;
}
.course-catalogue-content {
  padding: 10px 20px;
}
.course-catalogue-content .content_1 {
  margin: 10px 0;
}
.course-catalogue-content .content_1 .content-list {
  border: 1px solid #ccc;
  margin: 10px 0;
}
.course-catalogue-content .content_1 h2 {
  font-size: 16px;
  padding: 0 10px;
  margin-top: 40px;
}
.course-catalogue-content .content_1 .content-top .ant-col-6 {
  display: flex;
  text-align: center;
  font-size: 16px;
  justify-content: space-around;
}
.course-catalogue-content .content_1 .content-top .ant-col-6 dl {
  cursor: pointer;
}
.course-catalogue-content .content_1 .content-top .ant-col-6 dt {
  margin: 25px 0 10px;
}
.course-catalogue-content .content_1 .content-bottom {
  background: #eee;
  font-size: 12px;
  padding: 10px;
  border-top: 1px solid #ccc;
}
.course-catalogue-content .content_1 .content-bottom .ant-btn {
  margin-left: 10px;
  height: 25px;
  font-size: 12px;
  width: 60px;
  vertical-align: -1px;
  float: right;
}
